.breed-select {
  margin-bottom: 20px;
}

div.cat-info {
  gap: 10px;
  display: flex;
}

div.cat-info h2 {
  margin-top: 0;
}

div.cat-info img, div.cat-info div {
  width: 320px;
}

.hiden-selector {
  display: none !important;
}

.loader {
  color: #16b445;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 48px;
  font-weight: 400;
  display: inline-block;
  position: relative;
}

.loader:after {
  content: "Loading";
  z-index: 5;
  position: relative;
}

.loader:before {
  content: "";
  height: 6px;
  width: 100%;
  background: linear-gradient(#ff3d00 100%, rgba(0, 0, 0, 0) 0) 0 0 / 0% no-repeat;
  border: 1px solid;
  border-radius: 10px;
  animation: lineGrow 10s linear infinite;
  position: absolute;
  bottom: 0;
}

@keyframes lineGrow {
  to {
    background-size: 100%;
  }
}

